import React from 'react';
import '../css/A1_companyinfo.css'
import Header from './Haeder'
import Footer from './Footer'
import { motion } from "framer-motion";

function A1_CompanyInfo(props) {

  return (
    <div>

      {/* Haeder */}
      <Header></Header>

      <section className="companyinfo">
        
        <motion.div 
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }} 
          transition={{ duration: 1, delay: 0 }}
        >
        <div className="cover cover1">
          <div className="companyinfo_text empty"></div>
          <div className="companyinfo_text companyinfo_text1">GEORIM</div>
          <div className="companyinfo_text companyinfo_text2">     .</div>
        </div>
        </motion.div>

        <motion.div 
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }} 
          transition={{ duration: 1, delay: 0.3 }}
        >
        <div className="cover cover2">
          <div className="companyinfo_contentbox_center">         .</div>
          <div className="companyinfo_contentbox_center">         .</div>
          <div className="companyinfo_contentbox_center">     .</div>
          <div className="companyinfo_contentbox_center">        .</div>
        </div>
        </motion.div>

      </section>


      {/* Footer */}
      <Footer></Footer>

    </div>
  );
}

export default A1_CompanyInfo;