import React from 'react';
import '../css/A2_organization.css'
import Header from './Haeder'
import Footer from './Footer'
import { motion } from "framer-motion";

function A2_Organization(props) {
  return (

    <div>

      {/* Haeder */}
      <Header></Header>

      <section className="organization">
        
        <motion.div 
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }} 
          transition={{ duration: 1, delay: 0 }}
        >
        <div className="cover cover1">
          <div className="organization_text empty"></div>
          <div className="organization_text organization_text1">Group</div>
          <div className="organization_text organization_text2">    .</div>
        </div>
        </motion.div>

        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }} 
          transition={{ duration: 1, delay: 0.3}}
        >
        <div className="cover cover2">
          <div className="organization_contentbox box1">
            <div className="organization_box mainbox">()</div>
            <div className="organization_line"></div>
            <div className="organization_box"></div>
            <div className="organization_line"></div>
            <div className="organization_box">1</div>
            <div className="organization_line"></div>
            <div className="organization_box">2</div>
            <div className="organization_line"></div>
            <div className="organization_box">3</div>
          </div>
          <div className="organization_contentbox box2">
            <div className="organization_box mainbox">()</div>
            <div className="organization_line"></div>
            <div className="organization_box"></div>
            <div className="organization_line"></div>
            <div className="organization_box"></div>
            <div className="organization_line"></div>
            <div className="organization_box"></div>
            <div className="organization_line"></div>
            <div className="organization_box"></div>
          </div>
          <div className="organization_contentbox box3">
            <div className="organization_box mainbox"></div>
            <div className="organization_line"></div>
            <div className="organization_box">()</div>
            <div className="organization_line"></div>
            <div className="organization_box">()</div>
            <div className="organization_line"></div>
            <div className="organization_box">()</div>
            <div className="organization_line"></div>
            <div className="organization_box">()NS</div>
          </div>
        </div>
        </motion.div>

      </section>


      {/* Footer */}
      <Footer></Footer>

    </div>

    
  );
}

export default A2_Organization;